import Slider from './slider/Slider'
import Header from './components/Header'
import Menu from './components/Menu'
import Qualities from './components/Qualities'
import Team from './components/Team'
import Specialty from './components/Specialty'
import WhyUs from './components/WhyUs'
import Attorneys from './components/Attorneys'
import Faq from './components/Faq'
import Review from './components/Review'
import BlogPosts from './components/BlogPosts'
import Newsletter from './components/Newsletter'
import Footer from './components/Footer'
import AboutUs from './components/pages/AboutUs'
import Practice from './components/pages/Practice'
import ContactUs from './components/pages/ContactUs'
import Blog from './components/pages/Blog'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import './App.scss';
import './Style.scss';
import './MediaQuery.scss';

function App() {
  return (
    <Router>
    <div className="App">
        <Header />
        <Menu />
          <Route basename={window.location.pathname || ''} path="/" exact component={Slider} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Qualities} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Team} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Specialty} />
          <Route basename={window.location.pathname || ''} path="/" exact component={WhyUs} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Attorneys} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Faq} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Review} />
          <Route basename={window.location.pathname || ''} path="/" exact component={BlogPosts} />
          <Route basename={window.location.pathname || ''} path="/" exact component={Newsletter} />
        <Switch>
          <Route basename={window.location.pathname || ''} path="/about" component={AboutUs} />
          <Route basename={window.location.pathname || ''} path="/practice" component={Practice} />
          <Route basename={window.location.pathname || ''} path="/contact" component={ContactUs} />
          <Route basename={window.location.pathname || ''} path="/blog" component={Blog} />
        </Switch>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
