import { FaPlus, FaMinus } from 'react-icons/fa'
import Button from './Button'
import { motion } from 'framer-motion'

const Faqs= ({item, index, showAnswer, toggle}) => {
  return(
     <div id="faq">
          <div className="faq" key={index} onClick={() => toggle(index)}>
            <div className="question">
              <Button text={item.id} />
              <h5>{item.question}</h5>
              {showAnswer === index ? <FaMinus className="icon-minus" /> : <FaPlus className="icon-plus" /> } 
            </div>
            {showAnswer === index && <div className="answer">
              <motion.p
              animate ={{scale:1.05}} 
              >{item.answer}</motion.p>
            </div>} 
          </div>
        </div>
    )
}

export default Faqs